import React, { useEffect, useState } from "react";
import InputElement from "../../formElements/InputElement";
import { Form, Formik } from "formik";
import ButtonElement from "../../formElements/ButtonElement";
import { roleSchema } from "../../../util/Schema";
import { useSelector, useDispatch } from "react-redux";
import toast, { Toaster } from "react-hot-toast";
import ListIcon from "../../formElements/ListIcon";
import { useLocation, useNavigate } from "react-router-dom";
import SelectElement from "../../formElements/SelectElement";
import {
  state,
  cleanup,
  createRoles,
  updateRoles,
  getAllRoles,
  Roles,
} from "../../../redux/slices/RolesSlice";
import LineSpinner from "../../customLoader/LineSpinner";

const Role = () => {
  const [initial, setInitial] = useState({
    Role: "",
    inactive: "",
    id: "",
  });
  const [edit, setEdit] = useState(false);
  const { status, code } = useSelector(state);
  const roles = useSelector(Roles);

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    if (roles.length === 0) {
      dispatch(getAllRoles());
    }
  }, []);
  useEffect(() => {
    if (code === 201 || code === 200) {
      toast.success(status);
    } else if (code === 500 || code === 404 || code === 403) {
      toast.error(status);
    }

    return () => {
      dispatch(cleanup());
    };
  }, [code, status]);

  useEffect(() => {
    if (location.state?.roleid !== undefined && roles.length > 0) {
      const identifiedRole = roles.filter(
        (cat) => cat.id === location.state?.roleid
      );
      const initialState = {
        Role: identifiedRole[0].Role,
        inactive: identifiedRole[0].inactive,
        id: identifiedRole[0].id,
      };
      setInitial(initialState);
      setEdit(true);
    }

    return () => {
      location.state = null;
    };
  }, [location.state]);

  const navigationHandler = () => {
    navigate("../Role/List");
  };

  return (
    <>
      {status === "loading" && <LineSpinner />}
      <Toaster />
      <div className="px-20 py-8 overflow-auto flex  justify-center h-[calc(100vh-70px)]">
        <div className="  w-full md:w-1/3 px-4 ">
          <h1 className="text-center font-semibold text-3xl py-3">Roles</h1>
          <div className="w-80 py-3">
            <div
              className="float-end  text-xl text-gray-700 font-semibold cursor-pointer hover:text-orange-500"
              onClick={navigationHandler}
            >
              <ListIcon />
            </div>
          </div>

          <Formik
            initialValues={initial}
            enableReinitialize={true}
            validationSchema={roleSchema}
            onSubmit={(values, action) => {
              if (!edit) {
                dispatch(createRoles(values));
              } else {
                dispatch(updateRoles({ ...values }));
                setEdit(false);
              }

              action.setSubmitting(false);
              action.resetForm({
                values: {
                  Role: "",
                  inactive: "",
                  id: "",
                },
              });
            }}
          >
            {(props) => {
              return (
                <Form>
                  <div className=" flex flex-col  justify-center items-center  text-center mt-5 gap-3">
                    <InputElement
                      type="text"
                      placeholder="Role Name"
                      name="Role"
                    />

                    <SelectElement name="inactive">
                      <option value="">Inactive ? </option>
                      <option value="0">No</option>
                      <option value="1">Yes</option>
                    </SelectElement>

                    <ButtonElement
                      type="submit"
                      caption="SAVE"
                      disabled={props.isSubmitting}
                    />
                  </div>
                </Form>
              );
            }}
          </Formik>
        </div>
      </div>
    </>
  );
};

export default Role;
